import Mutator from './lib/iconimo.mutate.class';

class Iconimo {

    constructor() {
        console.log(`hoooray`);
        this.iconimos = Array.from(document.querySelectorAll(`[class^='iconimo-']`));
        this.observer = new MutationObserver(function(mutations) {
            mutations.forEach(function(mutation) {
                new Mutator(mutation.target, mutation.oldValue);
            });
        });

        this.observeConfig = {
            attributes: true,
            attributeOldValue : true,
            attributeFilter: [`class`],
        };

    }

    activate() {
        this.observe();
    }

    stop() {
        this.observer.disconnect();
    }

    observe() {
        this.iconimos.forEach((node) => {
            this.observer.observe(node, this.observeConfig);
        })
    }

    getIconimos() {
        return this.iconimos;
    }


}

export default Iconimo;